@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600&family=Poppins:wght@400;700&display=swap');

.home-container {
    font-family: 'Arial', sans-serif;
    color: #333;
    margin: 0;
    padding: 0;
}

.hero-section {
    background-color: #fff;
    text-align: center;
    padding: 5rem 3rem;
}

.hero-content h1 {
    color: #66a765;
    font-size: 2.5rem;
    margin-bottom: 1rem;      /* 1rem = 16px */
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
}

.hero-content p {
    font-size: 20px;
    color: #000;
    margin-bottom: 2.5rem;
    line-height: 1.6;
}

.explore-btn {
    background-color: #2b67b2;
    color: #fff;
    border: none;
    padding: 1rem 3rem;
    font-size: 1rem;
    border-radius: 30px;
    cursor: pointer;
    transition: background-color 0.3s;
    text-transform: uppercase;
}

.explore-btn:hover {
    background-color: #66a765;
}

/* Features Section */
.trust-section,
.features-section,
.insights-section {
    text-align: center;
    padding: 5rem 3rem;
    background-color: #fff;
}

.trust-section h2,
.features-section h2,
.insights-section h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #66a765;
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
}

.trust-section p,
.insights-section p {
    font-size: 20px;
    color: #000;
    margin-bottom: 2.5rem;
    line-height: 1.6;
}

.insight-grid,
.features-grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.feature-card,
.insight-card {
    background-color: #f9f9f9;
    margin: 1rem;
    padding: 2rem;
    width: 300px;
    border-radius: 12px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.feature-card p,
.insight-card p {
    font-size: 16px;
    margin-bottom: 1rem;
}

.feature-card h3,
.insight-card h3 {
    font-size: 22px;
    margin-bottom: 0.8rem;
}


/* Responsive */
@media (max-width: 768px) {
    .hero-content h1 {
        font-size: 36px;
    }

    .explore-btn {
        font-size: 12px;
    }

    .feature-card,
    .insight-card {
        width: 95%;
    }
}