.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    /* 60% blur */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    backdrop-filter: blur(6px);
    /* Background blur effect */
    overflow-y: auto;
    /* Enable vertical scrolling if needed */
}

.modal-content {
    background: white;
    padding: 30px;
    border-radius: 10px;
    height: 500px;      /* fixed height */
    width: 700px;       /* fixed width */
    text-align: center;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;     /* Hide overflow to create inner scroll */
    display: flex;
    flex-direction: column;
}

.modal-close {
    position: absolute;
    top: 15px;
    left: 35px;
    font-size: 22px;
    font-weight: bold;
    cursor: pointer;
    color: #000;
    z-index: 10;
}

/* Inner section now adjusts for scrollbar */
.modal-inner {
    flex: 1;
    /* Allow it to fill remaining height */
    overflow-y: auto;
    /* Scrollable content */
    margin-right: -15px;
    /* Ensures scrollbar doesn't cut into border */
    padding-right: 15px;
    /* Inner padding to avoid overlap */
    box-sizing: content-box;
}

/* Make scrollbar full height of modal */
.modal-inner::-webkit-scrollbar {
    width: 12px;
    /* Wider scrollbar for better aesthetics */
}

.modal-inner::-webkit-scrollbar-thumb {
    background-color: #aaa;
    border-radius: 10px;
    /* Rounded scrollbar thumb */
    border: 3px solid white;
}

.modal-inner::-webkit-scrollbar-track {
    background: transparent;
}


/* Mobile Responsive design */
@media (max-width: 768px) {
    .modal-content {
        width: 90%;
        height: 70%;
    }
}