.footer {
    background-color: #1e3932;
    color: #fff;
    padding: 2rem 0;
    font-family: 'Poppins', sans-serif;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;
    padding: 1rem;
}

.footer-column {
    flex: 1;
    min-width: 250px;
    margin: 1rem 0;
}

.footer-column h3 {
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.footer-column ul {
    list-style: none;
    padding: 0;
}

.footer-column ul li {
    margin: 0.5rem 0;
}

.footer-column ul li a {
    color: #ffffffcc;
    text-decoration: none;
    transition: color 0.3s;
}

.footer-column ul li a:hover {
    color: #66a765;
}

.footer-bottom {
    text-align: center;
    padding: 1rem 0;
    border-top: 1px solid #ffffff33;
    margin-top: 1.5rem;
    font-size: 0.9rem;
}

/* Responsive Design */
@media (max-width: 768px) {
    .footer-container {
        flex-direction: column;
        text-align: center;
    }
}