.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    background-color: #f9f9f9;
    /* Pastel Green */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.navbar-left {
    display: flex;
    align-items: center;
}

.navbar-logo {
    width: 45px;
    height: 45px;
    margin-right: 15px;
}

.navbar-title {
    font-size: 28px;
    color: #4caf50;
    margin: 0;
}

.navbar-links {
    list-style: none;
    display: flex;
    gap: 30px;
    margin: 0;
    padding: 0;
}

.navbar-links a {
    text-decoration: none;
    color: black;
    font-size: 18px;
    position: relative;
}

.navbar-links .active a {
    font-weight: bold;
    text-decoration: underline;
    color: #66a765;
}

.navbar-links a:hover {
    color: #66a765;
    /* Darker pastel green */
}

.start-scraping-btn {
    background-color: #2b67b2;
    color: white;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.start-scraping-btn:hover {
    background-color: #66a765;
}

.start-scraping-btn:active {
    background-color: #3170ba;
}


/* Hamburger Icon - Hidden on Desktop */
.hamburger {
    display: none;
    font-size: 1.8rem;
    cursor: pointer;
}

/* Mobile View Adjustments */
@media (max-width: 768px) {
    .navbar {
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .navbar-left {
        flex: 1;
    }

    .navbar-logo {
        height: 30px;
        width: 30px;
        /* Smaller logo on mobile */
    }

    .navbar-title {
        font-size: 22px;
        /* Smaller title on mobile */
    }

    .hamburger {
        display: block;
        font-size: 22px;
    }

    .navbar-links {
        display: none;
        flex-direction: column;
        width: 60%;
        text-align: center;
        text-size-adjust: 85%;
        padding: 15px 0;
        margin: 45px 0;
        background-color: white;
        border-top: 15px round #ddd;
        border-radius: 10px;
        position: absolute;
        top: 60px;
        right: 15px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        z-index: 1000;
        box-sizing: border-box;
    }

    .navbar-links.open {
        display: flex;
    }

    .navbar-right {
        width: 100%;
        text-align: center;
        margin-top: 10px;
    }

    .start-scraping-btn {
        width: 45%;
        font-size: 14px;
    }
}

