body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #fff;
  /* Pastel Green Background */
}

.App {
  text-align: center;
  color: black;
}

.navbar {
  background-color: white;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar .logo {
  font-size: 1.8rem;
  font-weight: bold;
  color: #00a86b;
  /* Green Text */
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

.nav-links a {
  text-decoration: none;
  color: black;
  font-size: 1rem;
}

.nav-links a:hover {
  color: #00a86b;
  /* Green Hover Effect */
}


