.ecommerce-container {
    font-family: 'Arial', sans-serif;
    background-color: #fff;
    color: #333;
    text-align: center;
    padding: 5rem 3rem;
    margin: 0;
}

.ecommerce-container h1 {
    color: #66a765;
    font-size: 2.5rem;
    margin-bottom: 1rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
}

.ecommerce-description {
    font-size: 20px;
    color: #000;
    margin-bottom: 2.5rem;
    line-height: 1.6;
}

.platform-grid {
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
}

.platform-card {
    width: 100%;
    max-width: 1000px;
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    padding: 30px;
    text-align: left;
    position: relative;
    opacity: 0;
    transform: translateY(40px);
    transition: all 0.7s ease;
}

/* Fade-in animation when card comes into view */
.platform-card.fade-in {
    opacity: 1;
    transform: translateY(0);
}

.platform-card.hidden {
    opacity: 0;
    transform: translateY(40px);
}

.platform-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.platform-logo {
    width: 55px;
    height: 55px;
    margin-right: 20px;
}

.platform-header h2 {
    font-family: 'poppins', sans-serif;
    font-size: 28px;
    color: #000;
    margin: 0;
    padding: 1rem;
}

.platform-description {
    font-family: 'arial', sans-serif;
    font-size: 18px;
    color: #555;
    line-height: 1.6;
}

.highlight {
    color: #66a765;
    font-weight: bold;
}

.scrape-btn-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
}

.scrape-btn {
    background-color: #2b67b2;
    color: #fff;
    width: 180px;
    text-align: center auto;
    padding: 12px 30px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.scrape-btn:hover {
    background-color: #66a765;
}

@media (max-width: 768px) {
    .platform-card {
        padding: 20px;
    }

    .platform-header h2 {
        font-size: 22px;
    }

    .platform-logo {
        width: 45px;
        height: 45px;
    }

    .scrape-btn {
        font-size: 14px;
        padding: 10px 25px;
    }
}